import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';
import { AngularFireAuthGuard, redirectUnauthorizedTo, redirectLoggedInTo } from '@angular/fire/auth-guard';
import { AuthenticationService } from './shared/authentication-service';


const redirectUnauthorizedToLogin = () => redirectUnauthorizedTo(['login']);
const redirectLoggedInToHome = () => redirectLoggedInTo(['dashboard']);
const routes: Routes = [
  {
    path: 'home',
    loadChildren: () => import('./home/home.module').then( m => m.HomePageModule)
  },
  {
    path: '',
    redirectTo: 'home',
    pathMatch: 'full'
  },
  {
    path: 'registration',
    loadChildren: () => import('./registration/registration.module').then( m => m.RegistrationPageModule)
  },
  {
    path: 'login', 
    loadChildren: () => import('./login/login.module').then( m => m.LoginPageModule)
  },
  {
    path: 'dashboard',
    loadChildren: () => import('./dashboard/dashboard.module').then( m => m.DashboardPageModule),
    canActivate:[AuthenticationService]
  },
  {
    path: 'clients',
    loadChildren: () => import('./clients/clients.module').then( m => m.ClientsPageModule),
    
  },
  {
    path: 'modal-clients',
    loadChildren: () => import('./modal-clients/modal-clients.module').then( m => m.ModalClientsPageModule),
   
  },
  {
    path: 'tabs-page',
    loadChildren: () => import('./tabs-page/tabs-page.module').then( m => m.TabsPagePageModule)
  },
  {
    path: 'dashboard/edit-users',
    loadChildren: () => import('./edit-users/edit-users.module').then( m => m.EditUsersPageModule),
    canActivate:[AuthenticationService]
  },
  {
    path: 'dashboard/admin-settings',
    loadChildren: () => import('./admin-settings/admin-settings.module').then( m => m.AdminSettingsPageModule),
    canActivate:[AuthenticationService]
  },
  {
    path: 'modal-edit-clients',
    loadChildren: () => import('./modal-edit-clients/modal-edit-clients.module').then( m => m.ModalEditClientsPageModule),
    canActivate:[AuthenticationService]
  },
  {
    path: 'diet',
    loadChildren: () => import('./see-diets-page/see-diets-page.module').then( m => m.SeeDietsPagePageModule),

    
  },
  {
    path: 'modal-editar',
    loadChildren: () => import('./modal-editar/modal-editar.module').then( m => m.ModalEditarPageModule)
  },
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules })
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
